import React, {useEffect} from 'react';
import Project from './Items/Project';
import forum from '../../assets/projects/forum.jpg';
import blog from '../../assets/projects/blog.jpg';
import portfolio from '../../assets/projects/portfolio.jpg';

const Projects = () => {
    useEffect(() => {
        //project animation
        const projects = document.querySelectorAll('.project-box');
        projects.forEach(project => {
            const observer = new IntersectionObserver((entries) => {
                observerCallback(entries, observer, project);
            }, {threshold: 0.3});
            observer.observe(project); 
        })
        const observerCallback = (entries, observer, header) => {
            entries.forEach((entry, i) => {
                 if (entry.isIntersecting) {
                    entry.target.classList.add('animate');
                 }
            });
        };
    }, []);

    return (
        <section id='projects' className='projects-wrapper'>
            <div className='projects'>
                <h2>Projetos</h2>
                <div className='project-list'>
                    <div className='project-box '>
                        <Project
                        text='Um f√≥rum SPA simples, por√©m com todas as funcionalidades de um f√≥rum normal. Desenvolvido com intuito de fixar e aprimorar meus conhecimentos em React e back-end. 
                        Possui autentica√ß√£o, valida√ß√£o, sistema de modera√ß√£o, tudo que algu√©m esperaria de um f√≥rum e o fato interessante de que n√£o √© preciso recarregar a p√°gina, o que garante mais velocidade e menos uso de dados.'
                        title='F√≥rumPlayers'
                        tags={['HTML', 'CSS', 'JavaScript', 'MongoDB', 'NodeJs', 'NPM', 'ExpressJs', 'React', 'Redux']}
                        imgPath={forum}/>
                    </div> 

                    <div className='project-box '>
                        <Project
                        text='Blog completo pensado para solidificar conhecimentos previamente adquiridos em nodeJs e banco de dados MySQL. Possui sistemar de publicar, editar e apagar posts, tamb√©m como login para administradores, e sistema de hierarquia, onde administradores n√£o podem alterar posts que n√£o tenham publicado, sendo isso poss√≠vel para o dono.'
                        title='TickTech Blog'
                        tags={['HTML', 'CSS', 'JavaScript', 'MySQL', 'NodeJs', 'NPM', 'ExpressJs']}
                        imgPath={blog}/>
                    </div> 
                    <div className='project-box '>
                        <Project
                        text='Portf√≥lio planejado com o foco no design para demonstrar minhas habilidades e projetos.'
                        title='Portf√≥lio'
                        tags={['HTML', 'CSS', 'JavaScript', 'React', 'NPM', 'Sass']}
                        imgPath={portfolio}/>
                    </div> 
                </div>
               
            </div>
            
        </section>
    )
}

export default Projects;
